@font-face {
  font-family: "Oswald";
  src: url("../Oswald-Regular.ttf") format('truetype');
  font-style: normal;
  font-weight: normal;
         
 }

 @font-face {
  font-family: 'Futura Book';
  src: url('../FuturaBookC.eot');
  src: url('../FuturaBookC.eot?#iefix') format('embedded-opentype'),
  url('../FuturaBookC.woff') format('woff'),
  url('../FuturaBookC.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
  }

 

.fon {
  position: fixed;
  background-repeat: repeat;
  width: 100%;
  height: 100%;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  display: -ms-flexbox;
  display: flex;
}

.grid {
  width: 85vmin;
  height: 85vmin;
  display: grid;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
  justify-items: center;
  -ms-flex-align: center;
      align-items: center;
  grid-template-columns: 50% 50%;
  grid-template-rows: 50% 50%;


}


.grid_item {
  -ms-flex-align: center;
      align-items: center;
  justify-items: center;
  /* display: flex; */
  background-color: rgba(227, 30, 37, 1);
  width: 35vmin;
  height: 35vmin;
  color: rgba(255, 255, 255,1);
  font-weight: bold;
  /* align-items: center;
  justify-content: center; */
  display: grid;
  grid-template-rows: 54% 15% 27%;
  grid-column-gap: 1em;
  -webkit-box-shadow: 0 0 1em rgba(0,0,0,0.5);
          box-shadow: 0 0 1em rgba(0,0,0,0.5);

}

.grid_item div {
  font-family: "Oswald", serif;
  text-align: center;
  
}
.zakaz {
  font-size: 4.8vmin;
}

.gotov {
  font-size: 6vmin;
}

.colpak {
  height: 80%;
}


.fade-enter {
  opacity: 0;
}

.fade-enter-active {
  opacity: 1;
  -webkit-transition: all 500ms ease-in;
  -o-transition: all 500ms ease-in;
  transition: all 500ms ease-in;
  
}
.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  opacity: 0;
  -webkit-transition: all 500ms ease-in;
  -o-transition: all 500ms ease-in;
  transition: all 500ms ease-in;
  
}


.block {
  font-family: "Futura Book", serif;
  position: absolute;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  background-color: rgba(227, 30, 37, 1);
  width: 35vmin;
  height: 35vmin;
  color: rgba(255, 255, 255,1);
  font-weight: 100;

  -ms-flex-align: center;

      align-items: center;
  justify-items: center;
  display: grid;
  grid-template-rows: 54% 15% 27%;
  grid-column-gap: 1em;
  -webkit-box-shadow: 0 0 1em rgba(0,0,0,0.5);
          box-shadow: 0 0 1em rgba(0,0,0,0.5);
  


}
.block:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  
}




